:root {
  --standard-main-color: #141414;
  --standard-green: #1e2827;
  --standard-white: #fff;
  --standard-secondary-text-color: #14141480;
}

body {
  font-family: Epilogue, sans-serif;
}

.header-container {
  background-color: var(--standard-main-color);
  width: 100%;
  z-index: 150;
  padding-top: 30px;
  padding-bottom: 30px;
  position: fixed;
}

.header-logo-menu-container {
  width: 100%;
  max-width: 375px;
  justify-content: space-between;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

.header-link {
  color: #fff;
  letter-spacing: -.36px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-decoration: none;
  display: flex;
}

.header-logo-svg {
  width: 14px;
  height: 14px;
  fill: #fff;
}

.header-menu-svg {
  width: 24px;
  height: 24px;
  fill: #fff;
}

.welcome-section {
  background-color: var(--standard-main-color);
  padding-top: 122px;
  padding-bottom: 40px;
}

.welcome-section-container {
  width: 100%;
  max-width: 375px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.welcome-section-title {
  max-width: 375px;
  color: #fff;
  letter-spacing: -1.52px;
  margin: 0;
  padding-bottom: 60px;
  font-size: 38px;
  font-weight: 600;
  line-height: 40px;
}

.welcome-section-about-info {
  float: right;
  width: 248px;
  color: #fff;
  letter-spacing: -.28px;
  padding-top: 40px;
  padding-bottom: 40px;
  font-size: 14px;
  font-style: normal;
  font-weight: 300;
  line-height: 18px;
}

.welcome-section-image {
  align-content: center;
}

.cascading-pictures {
  background-color: var(--standard-green);
  justify-content: center;
  display: flex;
}

.btn-container {
  justify-content: center;
  gap: 50px;
  display: flex;
}

.carousel-btn {
  width: 40px;
  height: 40px;
  border: 1px solid #ffffff80;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  transition: border-color .25s ease-in-out;
  display: flex;
}

.carousel-btn svg {
  width: 24px;
  height: 24px;
}

.carousel-btn:hover, .carousel-btn:focus {
  border-color: #fff;
}

.js-menu-container {
  display: none;
}

.menu-logo-close-btn-container {
  max-width: 375px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
  display: flex;
}

.js-menu-container {
  background-color: var(--standard-green);
  width: 100%;
  height: 100%;
  z-index: 160;
  flex-direction: column;
  align-items: center;
  gap: 200px;
  transition: right .8s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  right: -100%;
}

.js-menu-container.is-open {
  right: 0;
}

.js-menu-container .js-close-menu {
  cursor: pointer;
  background: none;
  border: none;
}

.js-close-menu svg {
  fill: #fff;
  width: 24px;
  height: 24px;
  transform: translate(0);
}

.menu-nav-address {
  flex-direction: column;
  gap: 14px;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  list-style-type: none;
  display: flex;
}

.menu-nav-address a {
  color: #fff;
  text-decoration: none;
  transition: color .25s ease-in-out;
}

.menu-nav-address a:hover {
  color: gray;
}

.menu-toggle > svg:hover {
  fill: #0ff;
}

.header-nav-address, .tel-btn {
  display: none;
}

@media screen and (min-width: 768px) {
  .header-logo-menu-container {
    max-width: 768px;
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .logo-nav-container {
    gap: 80px;
    display: flex;
  }

  .header-menu-svg {
    display: none;
  }

  .header-menu-svg:hover {
    cursor: pointer;
  }

  .header-nav-address {
    gap: 25px;
    font-size: 9px;
    line-height: 18px;
    list-style-type: none;
    display: flex;
  }

  .header-nav-address a {
    color: #fff;
    text-decoration: none;
  }

  .header-nav-address li {
    transition: transform .25s ease-in-out;
  }

  .header-nav-address li a {
    transition: text-shadow .25s ease-in-out;
  }

  .tel-btn:hover, .tel-btn:hover a {
    text-shadow: 1px 1px 2px #fff, 0 0 20px #0f0, 0 0 5px #008b56;
    transform: scale(1.1);
  }

  .header-nav-address li:hover, .header-nav-address li:hover a {
    text-shadow: 1px 1px 2px #fff, 0 0 20px #0f0, 0 0 5px #008b56;
    transform: scale(1.2);
  }

  .tel-btn {
    width: 159px;
    height: 48px;
    background-color: #1e2827;
    border-radius: 8px;
    justify-content: center;
    align-items: center;
    transition: transform .25s ease-in-out;
    display: flex;
  }

  .tel-btn a {
    color: #fff;
    font-size: 9px;
    text-decoration: none;
    transition: transform .25s ease-in-out, text-shadow .25s ease-in-out;
  }

  .swiper {
    float: left;
    width: 436px;
    justify-content: center;
    padding-bottom: 20px;
    display: flex;
  }

  .cascading-text-container {
    align-items: center;
    gap: 40px;
    padding-bottom: 40px;
    display: flex;
  }

  .welcome-section-container {
    width: 100%;
    max-width: 768px;
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
  }

  .welcome-section-title {
    width: 653px;
    max-width: 653px;
    font-size: 68px;
    font-weight: 600;
    line-height: 62px;
  }

  .welcome-section-about-info {
    padding-bottom: 150px;
  }
}

@media screen and (min-width: 1158px) {
  .header-logo-menu-container {
    max-width: 1158px;
  }

  .welcome-section-container {
    max-width: 1176px;
    position: relative;
  }

  .welcome-section-title {
    width: 750px;
    max-width: 750px;
    letter-spacing: -2.4px;
    font-size: 60px;
  }

  .welcome-section-about-info {
    width: 282px;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 0;
    right: 20px;
  }

  .welcome-section-main-picture {
    position: relative;
    right: -20px;
  }
}

